* {
  padding: 0;
  margin: 0;
  line-height: 1.2em;
}

h2 {
  color: red;
  font-weight: 600;
  font-size: 4rem;
}

h3 {
  font-size: 2.5rem;
}

p,
li {
  font-size: 1rem;
}

ul {
  padding: 6px 30px;
}

.logo-container {
  display: flex;
  justify-content: space-evenly;
}

.logo {
  width: 100px;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
